<template>
  <dynamic-form
    :key="rerender"
    :item="item"
    :title="title"
    :isEdit="isEdit"
    :onSave="onSave"
    :onCancel="onCancel"
    moduleName="network"
    :initialFormValues="formValues"
  ></dynamic-form>
</template>
<script>
import DynamicForm from '../../FormComponents/dynamicForm.vue';
export default {
  components: { DynamicForm },
  name: 'rf-network-form',
  props: ['isEdit', 'onCancel', 'onSave', 'item', 'isSaving'],
  data: () => ({
    formValues: {},
    rerender: 0,
  }),
  computed: {
    lowerCaseTitle() {
      return 'network';
    },
    titleCase() {
      return 'Network';
    },
    title() {
      return this.isEdit ? `Edit ${this.titleCase}` : `Create ${this.titleCase}`;
    },
    saveTitle() {
      return this.isEdit ? 'Save' : 'Create';
    },
  },
  mounted() {
    if (this.isEdit && this.item) {
      this.formValues = {
        ...this.item,
      };
      this.rerender++;
    }
  },
};
</script>
